<script>
export default {
  name: 'beginPage',
  data: function(){
    return{
      styleObj:{
        backgroundImage: 'linear-gradient(to right,$secondary-color 50%, $mid-color 50%)'
      }
    }
  },
  computed: {
    page () {
      return this.$route.path
    },
    pathBar () {
      if (this.page.indexOf('pay') > -1) {
        return 1
      }else if (this.page.indexOf('done') > -1){
        return 2
      }else{
        return 0
      }
    },
    percent(){
      if (this.page.indexOf('pay') > -1) {
        return '50%'
      }else if (this.page.indexOf('done') > -1){
        return '100%'
      }else{
        return '0%'
      }
    }
  }
}
</script>
