<script>
import image from "../assets/headphoneIMGL4494_TP_V4.png"
import image2 from "../assets/ipadpro-9_TP_V4.png"
import image3 from "../assets/nono.png"
import image4 from "../assets/nonono.png"
import btn1 from "../assets/icon_less-01.svg"
import btn2 from "../assets/icon_add-01.svg"
import btn3 from "../assets/order_btn_delete_normal-01.svg"

import payBtnBar from '@/components/payBtnBar.vue'

import bus from '../../to/bus'

export default {
  name: 'home',
  components: {
    payBtnBar
  },
  data: function () {
      return {
          btn1: btn1,
          btn2: btn2,
          btn3: btn3,
          test: 29289,
          listProducts: [
            { 
              title: 'NearStone 頂級監聽級耳罩式耳機',
              content: '創新的動圈式10毫米寬帶單體 實現了功率強勁 降低了聲學應力的影響 搭載...',
              Qty: 1,
              price: 2999,
              image: image,
              total: 2999,
            },
            { 
              title: 'Bpple 2018 QPad Air 128G 太空灰',
              content: '9.7吋 Retina顯示器 可拍攝 1080p 影片 120 萬像素 FaceTime HD 攝錄鏡頭...',
              Qty: 2,
              price: 12000,
              image: image2,
              total: 24000,
            },
            { 
              title: 'SOPOLA棒樂 三段風磁吸充電式風扇',
              content: '四葉風扇設計，風力很強勁 分離式磁吸底座，手持站立方便兩用 三段式風速...',
              Qty: 1,
              price: 790,
              image: image3,
              total: 790,
            },{ 
              title: '3.1A雙USB充電器感光小夜燈',
              content: '隱藏式插頭 高透光霧化燈罩 三段式控制開關 光控感應器 雙USB充電插孔 附...',
              Qty: 5,
              price: 300,
              image: image4,
              total: 1500,
            }
          ]
      }
  },
  computed:{
    tatal(){
      let cost = 0
      for(let i=0; i<this.listProducts.length; i++){
        cost+=this.listProducts[i].total
      }
      return cost
    }
  },
  methods: {
    addQty(num,ind){
      let pro = this.listProducts[ind]
      let test = pro.Qty+num
      if(test<1 || test>10)return
      pro.Qty = test
      pro.total = pro.Qty * pro.price
      this.test = this.tatal
      this.elementByValue()
    },
    cancel(id){
      if(this.listProducts.length<=1){
        alert('不能再刪了啦><')
        return
      }
      // this.listProducts[id].title='哈哈哈'
      this.listProducts.splice(id,1)
      this.test = this.tatal
      this.elementByValue()
    },
    elementByValue: function () {
      bus.$emit('val', this.tatal)
    }
  },
  mounted:function(){
    bus.$on("B-A",function(val){
        console.log(val)
    })
  }
}


// @ is an alias to /src
